import ZHeader from '@/components/common/ZHeader.vue'
import { newsList } from '../../api'
export default {
    name: 'news',
    components: {
        ZHeader
    },
    data() {
        return {
            newsList: [],
            page: 1,
            limit: 20,
            newsCategory: {
                '7': '常见问题',
                '8': '正证新闻',
                '14': '正证声明',
                '15': '用户协议',
                '16': '关于我们',
                '17': '行业资讯',
                '18': '免费查询',
                '21': '优惠信息'
            },
            loading: false,
            isOver: false,
            scroller: null
        }
    },
    mounted() {
        this.scroller = this.$refs.scroller
    },
    created() {
        newsList({ page: this.page, limit: this.limit, classid: this.$route.params.id }).then(res => {
            this.newsList = res
        })
    },
    computed: {
        title() {
            return this.newsCategory[this.$route.params.id] || '查询'
        }
    },
    methods: {
        loadMore() {
            if (!this.isOver) {
                this.loading = true
                newsList({ classid: this.$route.params.id, page: ++this.page, limit: this.limit }).then(res => {
                    this.loading = false
                    this.newsList.concat(res)
                    if (!res.length) {
                        this.isOver = true
                    }
                })
            }
        }
    }
}



