import Icon from '../common/Icon'
import Badge from 'vux/src/components/Badge'
import { mapGetters } from 'vuex'
export default {
    name: 'serviceBottom',
    // props: ['count'],
    props: ['item'],
    components: {
        Icon,
        Badge
    },
    data() {
        return {
            //cartnum: 0
        }
    },
    methods: {
        buynow() {
            this.$emit('buynow')
        },
        addtocart() {
            this.$emit('addtocart')
        }
    },
    computed: {
        ...mapGetters([
            'cartAllCounts',
        ])
    }
}



