export default {
    name: 'z-header',
    props: {
        showBorder: {
            type: Boolean,
            default: true
        },
        title: {
            type: String,
            // default: '主页'
        },
        showBack: {
            type: Boolean,
            default: false
        },
        showArrow: {
            type: Boolean,
            default: true
        },
        arrowColor: {
            type: String,
            default: '#e74037'
        },
        backWords: {
            type: String,
            default: '返回'
        },
        url: {
            type: String
        }
    },
    methods: {
        goback (url) {
          if(this.$route && url){
            this.$router.push(url)
          }else if(this.$route &&　!url){
              this.$router.go(-1)
          }else if(!this.$route && url){
              location.href='#/' + url
          }else{
              historu.go(-1)
          }
          this.$emit('goback')
        }
    }

}



