import Icon from '../common/Icon'
export default {
    name: 'commentItem',
    props: {
        comment: {
            type: Object
        }
    },
    computed: {
        avatar() {
            return this.comment.userPhoto ? this.comment.userPhoto : require('../../assets/imgs/avatar.jpg')
        }
    },
    data() {
        return {
            icon: 'icon-yiwancheng'
        }
    },
    components: {
        Icon
    }
}



