import XButton from 'vux/src/components/x-button'
export default{
    name: 'category-item',
    props: ['detail'],
    components: {
        XButton
    },
    methods: {
        go(item, index){
            this.$router.push({name:'serviceList',params:{id: item.parentId},query:{index: index, cid: item.catId}})
        }
    }
}



