
import Search from 'vux/src/components/search/index.vue'
import debounce from 'vux/src/tools/debounce/index.js'

import { hotKey, search } from '../../api'
import ServiceItem from '../store/ServiceItem'

export default {
    name: 'searchs',
    components: {
        Search,
        ServiceItem
    },
    data() {
        return {
            results: [],
            value: '',
            page: 1,
            limit: 30,
            serviceItems: [],
            loading: false,
            isOver: false,
            keys: [],
            scroller: null,
            timer: null,
            _debounce: null,
            key: ''
        }
    },
    created() {
        hotKey().then(res => {
            this.keys = res
        })
        this._debounce = debounce(() => {
            search({ keyword: this.key, type: 2, page: this.page, limit: this.limit }).then(res => {
                this.serviceItems = res
            })
        }, 500)
    },
    mounted() {
        this.scroller = this.$refs.scroller
    },
    beforeDestroyed(){
        if(this._debounce){
            this._debounce.cancel()
        }
    },
    methods: {
        resultClick(item) {
            window.alert('you click the result item: ' + JSON.stringify(item))
        },
        getResult(val) {
            this.results = val ? this.newResult(this.value) : []
        },
        newResult(val) {
            let rs = []
            for (let i = 0; i < 8; i++) {
                rs.push({
                    title: `${val} result: ${i + 1} `,
                    other: i
                })
            }
            return rs
        },
        searchList(val) {
            this.page = 1
            this.key = val
            this._debounce()
        },
        loadMore() {
            if (!this.isOver) {
                this.loading = true
                search({ keyword: this.key, type: 2, page: ++this.page, limit: this.limit }).then(res => {
                    this.loading = false
                    this.serviceItems.concat(res)
                    if (!res.length) {
                        this.isOver = true
                    }
                })
            }
        },
        searchBykey(val) {
            this.page = 1
            search({ keyword: val, type: 2, page: this.page, limit: this.limit }).then(res => {
                this.serviceItems = res
            })
        }
    }
}



