
import ZHeader from '@/components/common/ZHeader.vue'
import { getCategory, uploader, publishNeed } from '../../api'
import XInput from 'vux/src/components/X-input'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
    name: 'publish-need',
    components: {
        ZHeader,
        XInput,
        XTextarea,
        XButton,
        Toast
    },
    data() {
        return {
            categorys: [],
            selected: '',
            title: '',
            userPhone: '',
            uploadImgs: [],
            content: '',
            toastText: '',
            showToast: false
        }
    },
    created() {
        getCategory().then(res => {
            this.categorys = res
        })
    },
    methods: {
        uploader(e) {

            this.$store.dispatch('changeLoading')
            const file = e.target.files[0]
            const reader = new FileReader()
            let src = ''
            let url = window.URL || window.webkitURL
            if (url) {
                src = url.createObjectURL(file)
            } else {
                src = e.target.result
            }
            let _this = this
            reader.onload = (event) => {
                uploader({ upfile: event.target.result, type: 'users' }).then(photo => {
                    _this.$store.state.loading = false
                    _this.uploadImgs.push(photo)
                })
            }
            reader.readAsDataURL(file)
        },
        publish () {
            let valid = this.$refs.userPhone.valid
            if(!valid){
                this.validToast('手机号格式错误')
                return
            }
            let obj = {
                userid: this.$store.state.user.userId,
                userPhone: this.userPhone,
                title: this.title,
                content: this.content,
                pic: this.uploadImgs.join(','),
                catid: this.selected
            }
            publishNeed(obj).then(res => {
               this.validToast('发布成功')
            })
        },
        validToast(text) {
            this.toastText = text
            this.showToast = true
        },
    }
}



