import ZHeader from '@/components/common/ZHeader.vue'
export default {
    name: 'orderDetail',
    components: {
        ZHeader
    },
    data() {
        return {
            status: {
                '-5': '拒收',
                '-4': '拒收',
                '-3': '拒收',
                '-2': '待付款',
                '-1': '取消',
                '0': '待接收',
                '1': '待接收',
                '2': '已完成'
            },
            order: this.$store.state.currentOrder
        }
    },
    destroyed() {
        this.$store.dispatch('showFooter')
    },
    created() {
        this.$store.dispatch('showFooter')
    },
    pay(){

    }
}



