import ZHeader from '@/components/common/ZHeader.vue'
import XButton from 'vux/src/components/x-button'
export default {
    components: {
        ZHeader,
        XButton
    },
    methods: {
        store(){
            this.$router.push({name:'apply', query:{store: 1}})
        },
        user(){
            this.$router.push('apply')
        }
    }
}



