import ZHeader from '@/components/common/ZHeader.vue'
import {collectList} from '../../api'
import StoreItem from '../service/StoreItem'
export default {
    components: {
        ZHeader,
        StoreItem
    },
    data () {
        return {
           storeItems: [] 
        }
    },
    created () {
        collectList({userid: this.$store.state.user.userId, type: 1, limit: 50, page:1}).then(res => {
            this.storeItems = res
        })
    }
}



