export default{
    props: {
        icon: {
            type: String
        },
        vertical: {
            type: Boolean,
            default: true
        },
        horizon: {
            type: Boolean,
            default: false
        }
    },
    computed: {
        iconName () {
            return '#' + this.icon
        },
        margin () {
            if(this.horizon){
                return '10px'
            }
        }
    }
}



