import ZHeader from '@/components/common/ZHeader.vue'
import CateMenu from './CateMenu.vue'
import CategoryItem from './CategoryItem.vue'
import {getCategory} from '../../api'
export default {
    name: 'category',
    components: {
        ZHeader,
        CateMenu,
        CategoryItem
    },
    data () {
        return {
            items: [],
            detail: {}
        }
    },
    created () {
        getCategory().then(res => {
            this.items = res
            this.detail = this.items[0]
        })
    },
    methods: {
        checkedItem(detail){
            this.detail = detail
        }
    }
}



