import StoreItem from '../service/StoreItem.vue'
import ZHeader from '@/components/common/ZHeader.vue'
import { getCategory, getGoodsByCid, getStoreById } from '../../api'
export default{
    name: 'near',
    components: {
        StoreItem,
        ZHeader
    },
    data () {
        return {
            storeItems:[]
        }
    },
    created () {
        getStoreById({ id: 48, page: 1, limit: 10 }).then(res => {
        this.storeItems = res
    })
    }
}



