import ZHeader from '@/components/common/ZHeader.vue'
import { newsDetail } from '../../api'
export default {
    name: 'news',
    components: {
        ZHeader
    },
    data() {
        return {
            news: {},
        }
    },
    created() {
        newsDetail({ id: this.$route.params.id }).then(res => {
            this.news = res
        })
    }
}



