import Icon from '../common/Icon'
export default {
    name: 'checkbox',
    props: {
        value: {

        },
        id: {

        },
        checked: {
            type: Boolean,
            default: true
        },
        model:{

        },
        type: {

        },
        position: {

        }
    },
    components: {
        Icon
    },
    data() {
        return {
            changeModel: this.model
            //model: this.model,
            //  defaultType: this.type || 'checkbox'
        }
    },
    methods: {
        change(value) {
            this.$emit('change', value)
        }
    }
}



