import ServiceItem from './ServiceItem'
import CheckCard from './CheckCard'
export default {
    props: ['items', 'serviceItems'],
    components: {
        ServiceItem,
        CheckCard
    },
    methods: {
        checkItem(item, index){
            this.$emit('checkItem', item, index)
        }
    }
}



